import {useForm} from "react-hook-form";
import { useRecoilValue, useSetRecoilState } from "recoil";
import { categoryState, ToDoState } from "../atoms";
import styled from "styled-components";

const StyledForm = styled.form`
    border-radius: 20px;
    width: 100%;
    border: 1px solid ${props=>props.theme.textColor};
    display: flex;
    input{
        width: 100%;
        padding: 16px;
        background: transparent;
        color: ${props=>props.theme.accentColor};
        outline: none;
        border-radius: 20px;
        cursor: text;
        &:focus, &:active{
            color: ${props=>props.theme.textColor};
        }
    }
    button{        
        display: flex;
        justify-content: center;
        align-items: center;
        background: none;
        padding: 16px 0;
        margin-left: 0;
        width: 40px;
        svg{
            fill:${props=>props.theme.textColor};
            cursor: pointer;
        }
    }
`;

interface IForm{
    toDo: string;
}

function CreateToDo(){
    const setToDos = useSetRecoilState(ToDoState);
    const category = useRecoilValue(categoryState);
    const {
        register, handleSubmit, setValue
    } = useForm<IForm>();
    const onSubmit = ({toDo}:IForm)=>{
        // console.log(data.toDo);
        setToDos((oldToDos) => 
            [{
                text: toDo,
                id: Date.now(),
                category,
            }, 
            ...oldToDos
        ]
        );
        setValue("toDo", ""); //submit 후 값 비워줌
    };
    const toDos = useRecoilValue(ToDoState);
    localStorage.setItem("toDos", JSON.stringify(toDos));

    return(
        <StyledForm onSubmit={handleSubmit(onSubmit)}>
                <input {...register("toDo",{
                    required: "Please write a to do",

                })}
                placeholder="Write a to do">
                </input>
                <button>
                    <svg fill="" width="24px" height="24px" viewBox="0 0 512 512" xmlns="http://www.w3.org/2000/svg"><path d="M256,48C141.31,48,48,141.31,48,256s93.31,208,208,208,208-93.31,208-208S370.69,48,256,48Zm80,224H272v64a16,16,0,0,1-32,0V272H176a16,16,0,0,1,0-32h64V176a16,16,0,0,1,32,0v64h64a16,16,0,0,1,0,32Z"/></svg>
                </button>
            </StyledForm>
    );
}

export default CreateToDo;