import { createGlobalStyle } from "styled-components";

export const GlobalStyle = createGlobalStyle` //css reset해줌 - 기존에 하던 리셋들 보고 추가하기
@font-face {
    font-family: "Wanted Sans Variable";
    font-style: normal;
    font-display: swap;
    font-weight: 400 1000;
    src: url("../fonts/WantedSansVariable.woff2") format('font-woff2');
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}
*[hidden] {
    display: none;
}
body {
  line-height: 1;
  font-family: "Inter", "Wanted Sans Variable", "Noto Sans KR", "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 600;    
  background-color: ${props => props.theme.bgColor};
  color: ${props=>props.theme.textColor};
  transition: background-color .3s ease-in, color .3s ease-in;
}
menu, ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a{
  text-decoration: none;
  color: inherit; //링크 파란색, 보라색 없애고 부모 css속성 따라감
}
button{
  cursor: pointer;
  border: 0;
  border-radius: 20px;
  width: 60px;
  height: 28px;
  background-color: ${props => props.theme.textColor};
  color: ${props => props.theme.bgColor};
  font-family: "Wanted Sans Variable";
  font-weight: 600;
  font-size: 0.75rem;
}
input, select{
  height: 28px;
  border: 0;
  font-family: "Wanted Sans Variable";
  font-weight: 600;
  font-size: 0.75rem;
}
li{
  list-style: none;
}
*{
  box-sizing: border-box;
  cursor: default;
}
`;